import Block from './Block.js';
import socket from './socket.js';

// 
class Chess extends Block {
  constructor(x, y, color, player, SPACE_WIDTH, SQUARE_WIDTH) {
    super(x, y, SPACE_WIDTH, SQUARE_WIDTH);
    this.type = 'CHESS';
    this.id = player;
    this.color = color; // 
    this.textColor = '#FFFFFF'; // 
    this.radius = SQUARE_WIDTH / 2; // 
    this.pointX = this.left + SQUARE_WIDTH / 2; // x
    this.pointY = this.top + SQUARE_WIDTH / 2; // y
    this.plankCount = 10; // 10
  }
  // 
  updatePosition(x, y) {
    this.x = x;
    this.y = y;
    this.left = Math.floor(x / 2) * (this.SPACE_WIDTH + this.SQUARE_WIDTH) + (x % 2) * this.SPACE_WIDTH; // x
    this.top = Math.floor(y / 2) * (this.SPACE_WIDTH + this.SQUARE_WIDTH) + (y % 2) * this.SPACE_WIDTH; // y
    this.pointX = this.left + this.SQUARE_WIDTH / 2;
    this.pointY = this.top + this.SQUARE_WIDTH / 2;
  }
  // 
  updatePlankCount(count) {
    this.plankCount = count;
  }
  // 
  changeActionPlayer(plankIndex) {
    let data = {
      x: this.x,
      y: this.y,
      plankCount: this.plankCount, // 
      plankIndex: plankIndex ? plankIndex : [], // 
    }
    socket.emit('changeActionPlayer', data);
  }
  // 
  gameover() {
    let data = {
      id: this.id,
    }
    socket.emit('gameover', data);
  }
  // leave room
  leaveRoom(roomName) {
    let data = {
      roomName: roomName,
    }
    socket.emit('leaveRoom', data);
  }
}

export default Chess;