
import Block from './Block.js';

// 
class Plank extends Block {
  constructor(x, y, SPACE_WIDTH, SQUARE_WIDTH) {
    super(x, y, SPACE_WIDTH, SQUARE_WIDTH);
    this.type = 'PLANK'; // 
    this.color = '#ECBF7E'; // 
    this.status = 1; // 
    this.putDownAble = false; // 
  }
  hoverPlank(x, y) {
    if (this.status != 1 && this.status != 2) {
      return;
    }
    if (this.isInRect(x, y)) {
      this.changeStatus(2);
    } else {
      this.changeStatus(1);
    }
  }
  // 
  changeStatus(status) {
    if (this.status === 0) { // 0
      return;
    }
    this.status = status;
    switch (status) {
      case 1: // 
        this.color = '#ECBF7E';
        break;
      case 2: // hover
        this.color = '#C78F3E';
        break;
      case 3: // 
        this.color = '#00FF00';
        break;
      case 4: // 
        this.color = '#90FF90';
        break;
      case 5: // 
        this.color = '#FF9090';
        break;
      case 0: // 
      default:
        this.color = '#000000';
        break;
    }
  }
}

export default Plank;