
let util = {}

util.$ = (el) => {
  return document.querySelector(el);
}

/**
 * class
 * @param  {object}     obj      dom
 * @param  {string}     cls      class
 */
util.addClass = (obj, cls) => {
  let obj_class = obj.className; //  class 
  let blank = (obj_class != '') ? ' ' : ''; //  class , ''
  let added = obj_class + blank + cls; //  class  class
  obj.className = added; //  class
}

/**
 * class
 * @param  {object}     obj      dom
 * @param  {string}     cls      class
 */
util.removeClass = (obj, cls) => {
  let obj_class = ' ' + obj.className + ' '; //  class , 
  obj_class = obj_class.replace(/(\s+)/gi, ' '); // 
  let reg = new RegExp(' ' + cls + ' ', 'g');
  let removed = obj_class.replace(' ' + cls + ' ', ' '); //  class  class
  while (reg.test(removed)) {
    removed = removed.replace(reg, ' ');
  }
  removed = removed.replace(/(^\s+)|(\s+$)/g, ''); // 
  obj.className = removed; //  class
}

/**
 * class
 * @param  {object}     obj       dom
 * @param  {string}     cls       class
 * @return {Boolean}              true
 */
util.hasClass = (obj, cls) => {
  let obj_class = obj.className; //  class 
  let obj_class_lst = obj_class.split(/\s+/); // splitcls
  let x = 0;
  for (x in obj_class_lst) {
    if (obj_class_lst[x] == cls) { // , cls
      return true;
    }
  }
  return false;
}

/**
 * 
 * @param  {string | number} value      
 * @param  {string} format  YY: , MM: , DD: , hh: , mm: , ss: 
 *                  'YY-MM-DD hh:mm:ss'
 * @return {[type]}            [description]
 */
util.timeFilter = (value, format) => {
  if (value === null) {
    return null;
  }
  let out;
  let time = new Date();
  time.setTime(value);
  let YY = time.getFullYear(),
    MM = (time.getMonth() + 1) < 10 ? ('0' + ((time.getMonth() + 1) + '')) : (time.getMonth() + 1),
    DD = time.getDate() < 10 ? ('0' + (time.getDate() + '')) : time.getDate(),
    hh = time.getHours() < 10 ? ('0' + (time.getHours() + '')) : time.getHours(),
    mm = time.getMinutes() < 10 ? ('0' + (time.getMinutes() + '')) : time.getMinutes(),
    ss = time.getSeconds() < 10 ? ('0' + (time.getSeconds() + '')) : time.getSeconds();
  out = format ?
    format.replace(/YY/g, YY).replace(/MM/g, MM).replace(/DD/g, DD).replace(/hh/g, hh).replace(/mm/g, mm).replace(/ss/g, ss) :
    YY + '-' + MM + '-' + DD + ' ' + hh + ':' + mm + ':' + ss;
  return out;
}

export default util;